.container {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  min-width: 280px;
  margin-inline: auto;
  padding-inline: 20px;
  display: grid;
}

@media (width >= 567px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1024px) {
  .container {
    margin-inline: auto;
    padding-inline: 55px;
  }
}

@media (width >= 567px) {
  .container {
    --columns: 6;
    column-gap: 20px;
  }
}

@media (width >= 1024px) {
  .container {
    --columns: 12;
    column-gap: 30px;
  }
}

