html, body {
  scroll-behavior: smooth;
  background: #f1f5f4;
}

.page {
  position: relative;
}

.page__body {
  min-width: 280px;
  margin: 0;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  overflow: hidden;
  transform: translateX(0);
}

.top-bar {
  z-index: 0;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  display: flex;
  position: sticky;
}

