}

.top-bar__logo {
  object-fit: cover;
  width: 123px;
  height: 40px;
  transition: transform .3s;
  display: flex;
}

.top-bar__logo:hover {
  transform: scale(1.2);
}

@media (width >= 567px) {
  .top-bar__logo {
