.nav {
  position: relative;
}

.nav__list {
  flex-direction: column;
  gap: 32px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list:after {
  content: "";
  background-color: #f1f5f4;
  width: 100%;
  height: 1px;
  display: block;
  position: absolute;
  bottom: -40px;
  transform: scale(1);
}

@media (width >= 567px) {
  .nav__list:after {
    bottom: -50px;
  }
}

.nav__list--footer {
  gap: 5px;
  margin: 0;
  padding: 0;
}

.nav__list--footer:after {
  display: none;
}

.nav__link {
  color: #f1f5f4;
  letter-spacing: 2px;
  font-size: 22px;
  line-height: 22px;
  text-decoration: none;
  position: relative;
}

.nav__link:after {
  content: "";
  transform-origin: 0;
  background-color: #f1f5f4;
  width: 100%;
  height: 1px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.nav__link:hover:after {
  transform: scale(1);
}

@media (width >= 567px) {
  .nav__link {
    font-size: 48px;
    font-weight: 700;
    line-height: 57.6px;
  }
}

.nav__link--footer {
  color: #f1f5f4;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
  position: relative;
}

.nav__link--footer:after {
  content: "";
  transform-origin: 0;
  background-color: #f1f5f4;
  width: 100%;
  height: 1px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -4px;
  transform: scale(0);
}

.nav__link--footer:hover:after {
  transform: scale(1);
}

